import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import {
  ApolloClient,
  InMemoryCache,
  ApolloProvider,
  useQuery,
  gql
} from "@apollo/client";
import moment from 'moment';
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';

const client = new ApolloClient({
  uri: 'http://192.168.1.2:8282/v1/graphql',
  cache: new InMemoryCache()
});

function App() {

  const [data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      const result = await client
        .query({
          query: gql`
            query MyQuery {
              weather_data {
                humidity
                created_at
                id
                location
                temperature_f
              }
            }    
          `
        })

      setData(result.data.weather_data.filter((_e:any,i:number)=> i%10 === 0).map((e: any) => ({ ...e, name: moment(e.created_at).format('MMMM D, h:mm a') })));
      console.log(result)
    })()
  }, [])
  return (
    <div style={{ width: '100%', height: '100%' }}>
      <ResponsiveContainer>
        <LineChart data={data} margin={{ top: 5, right: 20, bottom: 5, left: 0 }}>
          <Line type="monotone" dataKey="temperature_f" stroke="#8884d8" />
          <Line type="monotone" dataKey="humidity" stroke="#8884d8" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
}

export default App;
